@media screen and (min-width: 1350px) {
    .home-section-1 {
        height: calc(100vh - 126px);
        padding: 0 $padding-desktop;

        .section-1-wrapper {
            flex-direction: row;
            justify-content: space-between;
            align-items: flex-end;
            margin: calc(35vh - 99px) 0 auto 0;

            h1 {
                font-size: 8.3rem;
                text-align: left;
            }
    
            p {
                text-align: left;
                font-size: 1.5em;
                position: relative;
                line-height: 2.5rem;
                margin: 0 0 0 2em;
    
                &:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    top: -3em;
                    width: 50px;
                    height: 4px;
                    background: $rapture-blue;
                }
            }
        }

        &:before {
            content: '';
            position: absolute;
            top: calc(35vh - 99px);
            left: -100px;
            width: 200px;
            height: 200px;
            background-image: url("../images/bg-pattern-home-1.svg");
            background-repeat: no-repeat;
            display: block;
            z-index: -1;
        }

        &:after {
            bottom: 0;
            right: $padding-desktop;
        }
    }

    .home-section-2 {
        padding: 11em $padding-desktop;

        .section-2-wrapper {
            flex-direction: row;
            align-items: flex-start;
            justify-content: space-between;

            h2 {
                font-size: 4rem;
            }

            .presentation {
                padding: 0 0 0 9em;

                article {
                    img {
                        align-self: start;
                    }

                    h3{
                        padding: 0 0 1em 0;
                    }
                }
            }
        }

        &:after {
            top: unset;
            bottom: 0;
        }
    }

    .home-section-3 {
        padding: 11.5em $padding-desktop;

        .section-3-wrapper {
            h2 {
                padding: 0 1em;
                font-size: 4rem;
            }

            .testimonials {
                flex-direction: row;
                justify-content: space-between;
                margin: 5em 0 0 0;

                figure {
                    padding: 0;
                    max-width: 350px;
                    margin: 0;

                    &:last-child {
                        padding: 0;
                    }

                    &:not(:first-child) {
                        margin: 0 0 0 2em;
                    }

                    &:before {
                        top: -2.8em;
                    }

                    figcaption {
                        cite {
                            &:nth-child(2) {
                                padding: .5em 0 2.3em 0;
                            }
                        }
                    }
                }
            }
        }
    }
}